/* App.css */

/* Container styles */
.App {
  font-family: Arial, sans-serif;
  margin: 0 auto;
  padding: 20px;
  background-color: #f4f4f4;
  max-width: 400px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

/* Header styles */
h1 {
  text-align: center;
  color: #333;
}

/* Input container styles */
.input-container {
  display: flex;
  margin-bottom: 20px;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  margin-right: 10px;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* Todo list styles */
.todo-list {
  list-style-type: none;
  padding: 0;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 5px;
  background-color: #fff;
  cursor: pointer;
}

.todo-item.completed {
  background-color: #e0e0e0;
  text-decoration: line-through;
}

/* Reset button styles */
.reset-button-container {
  text-align: center;
}

.reset-button {
  background-color: #ff6347;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.reset-button:hover {
  background-color: #ff4136;
}

/* Select and Delete buttons */
.select-delete-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
}

.button:hover {
  background-color: #181b1f;
}

/* Add this rule to prevent hover effect on completed TODOs */
.button:hover:not(.completed) {
  background-color: #18191a;
}
